.advantages {
  padding-top: 17rem;
  padding-bottom: 17rem;
}

.advantages__columns-wrap {
  display: flex;
  justify-content: space-between;
}

.advantages__img-column {
  display: grid;

  grid-template-areas:
    "f f s s"
    "f f s s"
    "t t s s"
    "t t h h"
    "t t h h";

  grid-column-gap: 3.5rem;
  grid-row-gap: 3rem;

  width: 67.5rem;
  height: 72.5rem;
}

.advantages__img {
  .img-cover;

  &:nth-child(1) {
    grid-area: f;

  }
  &:nth-child(2) {
    grid-area: s;
  }
  &:nth-child(3) {
    grid-area: t;
  }
  &:nth-child(4) {
    grid-area: h;
  }
}

.advantages__content-wrap {
  width: 63.5rem;
}

